(function (diskupon) {
    diskupon.App.module("Deals", function(Deals, App, Backbone, Marionette, $, _) {

        var submodules = ['Deals.List', 'Deals.Details'];

        Deals.on('start', function () {
            Deals.listLayout = new App.HomePageLayout();
            Deals.detailsLayout = new App.DetailsPageLayout();
        });

        Deals.Controller = Marionette.Controller.extend({
            initialize: function (options) {

            },

            _startListSubmodule: function () {
                Deals.listLayout.render();
                App.regionManager.get('Main').show(Deals.listLayout);
                var options = {
                    DealsListRegion: Deals.listLayout.MainContent,
                    DealsListMobileRegion: Deals.listLayout.MainContentMobile
                };
                App.vent.trigger('submodule:start', submodules, 'Deals.List', options);
            },

            _startDetailsModule: function () {
                Deals.detailsLayout.render();
                App.regionManager.get('Main').show(Deals.detailsLayout);
                var options = {
                    DealsDetailsRegion: Deals.detailsLayout.MainContent,
                    DealsDetailsMobileRegion: Deals.detailsLayout.MainContentMobile
                };
                App.vent.trigger('submodule:start', submodules, 'Deals.Details', options);
            },

            showDealsList: function (mode, categoryId) {
                //Honeymoon: -66
                this._startListSubmodule();
                switch (mode) {
                case diskupon.DealCategory.All:
                    App.Deals.List.controller.SearchParams.CategoryId = diskupon.DealCategory.All;
                    break;
                case diskupon.DealCategory.New:
                    App.Deals.List.controller.SearchParams.CategoryId = diskupon.DealCategory.New;
                    break;
                case diskupon.DealCategory.Hot:
                    App.Deals.List.controller.SearchParams.CategoryId = diskupon.DealCategory.Hot;
                    break;
                case diskupon.DealCategory.EndingSoon:
                    App.Deals.List.controller.SearchParams.CategoryId = diskupon.DealCategory.EndingSoon;
                    break;
                case diskupon.DealCategory.ByCategory:
                    App.Deals.List.controller.SearchParams.CategoryId = categoryId;
                    break;
                default:
                    App.Deals.List.controller.SearchParams.CategoryId = diskupon.DealCategory.All;
                    break;
                }
                App.Deals.List.controller.showDeals();
            },

            showDealDetails: function (dealId) {
                this._startDetailsModule();
                App.Deals.Details.controller.DealId = dealId;
                App.Deals.Details.controller.showDealsDetails();
            }
        });

        Deals.addInitializer(function () {
            //console.log('Deals.Initializer');
            Deals.controller = new Deals.Controller({
            });
        });

        Deals.addFinalizer(function () {
            //console.log('Deals.Finalizer');
            Deals.controller.close();
            delete Deals.controller;
        });

    });
} (window.Diskupon));