/********************************/
/*
/* Diskupon Starts Here
/*
/********************************/
window.Diskupon = {
    DealCategory: {
        All: -11,
        New: -22,
        Hot: -33,
        EndingSoon: -44,
        ByCategory: -55,
        Honeymoon: -66
    },
    API_HOST: 'diskupon.apphb.com', //'diskupon.apphb.com',
    requestUrl: function (slug) {
        if (slug.substring(0, 1) === '/') {
            return '//' + this.API_HOST + slug;
        } else {
            return '//' + this.API_HOST + '/api/' + slug;
        }
    }
};

(function (diskupon) {
    diskupon.App = new Marionette.Application();
    var App = diskupon.App;

    App.regionManager = new Marionette.RegionManager();
    App.regionManager.addRegions({
        Header: '#dsk_header',
        RegionSelector: '#dsk_pick_region',
        TopMenu: '#dsk_topMenu',
        TopMenuInner: '#dsk_topMenu_Inner',
        TopMenuInnerMobile: '#dsk_topMenu_Inner_Mobile',
        Main: '#dsk_main',
        Footer: '#dsk_footer'
    });

    App.on('start', function () {
        if (Backbone.history){
            Backbone.history.start({ pushState: true });
            others.prepareBackbonePushState();
        }
    });

    App.HomePageLayout = Backbone.Marionette.Layout.extend({
        template: "#homePageLayout_template",
        regions: {
            HomeSlides: '#dsk_homeSlides',
            MainContent: '#dsk_content',
            MainContentMobile: '#dsk_content_mobile'
        },
        className: 'container'
    });

    App.DetailsPageLayout = Backbone.Marionette.Layout.extend({
        template: "#detailsPageLayout1_template",
        regions: {
            MainContent: '#dsk_content',
            MainContentMobile: '#dsk_content_mobile'
        },
        className: 'container'
    });

    App.vent.on('submodule:start', function (submodulesNames, submoduleName, options) {
        //console.log('ON:submodule:start NAME >> ' + submoduleName);
        for (var i = 0; i < submodulesNames.length; i++) {
            var _submoduleName = submodulesNames[i];
            if (_submoduleName !== submoduleName) {
                //console.log('ON:submodule:start STOPPING >> ' + _submoduleName);
                App.module(_submoduleName).stop();
            } else {
                //console.log('ON:submodule:start STARTING >> ' + _submoduleName);
                App.module(_submoduleName).start(options);
            }
        }
    });

    App.SelectedRegionId = {
        get: function () {
            var ls = localStorage.getItem('ds_regionid');
            if (ls === null) {
                ls = '2'; // change this with jakarta id
            }
            var selectedRegionId = parseInt(ls, 10);
            return selectedRegionId;
        },
        set: function (value) {
            localStorage.setItem('ds_regionid', value.toString());
        }
    };

} (window.Diskupon));